html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: bebas;
  src: url("../fonts/bebas.woff2") format("woff2"), url("../fonts/bebas.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: helvetica;
  src: url("../fonts/helvetica.woff2") format("woff2"), url("../fonts/helvetica.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: helveticanueu;
  src: url("../fonts/helveticaneue-bold.woff2") format("woff2"), url("../fonts/helveticaneue-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

header {
  font-family: bebas;
}

body {
  background: #FFF;
  line-height: 1.4;
  line-height-font-family: helvetica, Arial, sans-serif;
}

html {
  font-size: 12px;
  font-family: helvetica, Arial, sans-serif;
  color: #464646;
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

.container--header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.posts {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.posts__container {
  max-width: 670px;
}

.post__preview {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.container--header {
  padding-top: 10px;
  margin-bottom: 20px;
}

.logo {
  color: #e8603c;
  font-size: 36px;
  text-decoration: none;
}

.logo:hover {
  text-decoration: underline;
}

.logo--end {
  color: #f3af9d;
}

.nav {
  font-family: bebas, Roboto, Open-Sans, sans-serif;
  font-size: 16px;
  padding-top: 20px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.nav__item {
  margin-left: 30px;
}

.nav__link {
  text-decoration: none;
  color: #b9b9b9;
}

.nav__link:first-child {
  margin-left: 0;
}

.nav__link:hover {
  text-decoration: underline;
  color: #3a3a3a;
}

.nav__link--isactive {
  color: #e8603c;
}

.title {
  display: block;
  height: 60px;
  width: 100%;
  background-color: #e8603c;
  font-family: bebas, Roboto, Open-Sans, sans-serif;
  text-transform: uppercase;
  color: #fff;
  font-size: 24px;
  position: relative;
  padding: 10px 20px;
  margin-bottom: 20px;
}

.title::before, .title::after {
  content: '';
  height: 60px;
  background-color: #f3af9d;
  width: 100%;
  display: block;
  position: absolute;
  top: 0;
}

.title:after {
  margin-left: 10px;
  left: 100%;
}

.title::before {
  margin-right: 10px;
  right: 100%;
}

.post::after {
  content: '';
  display: block;
  width: 100%;
  height: 15px;
  background-image: url(../img/post_pt.jpg);
  margin-bottom: 50px;
}

.post__data__info {
  min-width: 145px;
  padding-top: 20px;
  align-items: flex-start;
}

.post__data__title {
  text-transform: uppercase;
  color: #e8603c;
  font-family: bebas, Roboto, Open-Sans, sans-serif;
  margin-bottom: 10px;
  font-size: 12px;
}

.post__data__text {
  color: #b9b9b9;
  font-family: helvetica, Arial, sans-serif;
  font-size: 12px;
  margin-bottom: 10px;
  text-decoration: none;
}

.post__data__text:hover {
  text-decoration: underline;
  color: black;
}

.post__data__link {
  text-decoration: none;
  color: #b9b9b9;
  margin-left: 5px;
}

.post__data__link::before {
  content: '|';
  color: inherit;
  font-size: inherit;
  margin-right: 5px;
}

.post__data__link:first-child {
  margin-left: 0;
  padding: 0;
}

.post__data__link:first-child::before {
  content: '';
  font-size: 0;
}

.post__data__link:hover {
  text-decoration: underline;
  color: #3a3a3a;
}

.post__data__link:active {
  color: #e8603c;
}

.post__content {
  padding: 5px;
  padding-top: 20px;
  margin-bottom: 10px;
  text-align: left;
}

.post__content__title {
  text-transform: uppercase;
  color: #e8603c;
  font-size: 18px;
  font-family: bebas, Roboto, Open-Sans, sans-serif;
  margin-bottom: 10px;
}

.post__content__text {
  color: #b9b9b9;
  font-family: helvetica, Arial, sans-serif;
  font-size: 12px;
  margin-bottom: 50px;
  color: #464646;
}

.post__content__text > p {
  margin-bottom: 15px;
  line-height: 1.6;
}

.post__content__link {
  text-decoration: none;
  color: #b9b9b9;
  position: relative;
  top: -10px;
}

.post__content__link:hover {
  text-decoration: underline;
  color: #3a3a3a;
}

.post__content__link:active {
  color: #e8603c;
}

.post__preview {
  padding-bottom: 20px;
}

.post__simple__text {
  background-color: #f1f1f1;
  font-style: italic;
  padding-top: 25px;
  padding-right: 40px;
  padding-bottom: 20px;
  padding-left: 80px;
  display: inline-block;
  margin-bottom: 50px;
  position: relative;
  letter-spacing: .4px;
}

.post__simple__text::before {
  content: '';
  width: 35px;
  height: 20px;
  display: block;
  background-image: url(../img/quotes.svg);
  background-repeat: no-repeat;
  display: inline-block;
  position: absolute;
  left: 35px;
  font-size: 13px;
}

.pagination {
  display: grid;
  grid-template-columns: repeat(3, minmax(30px, max-content));
  grid-gap: 5px;
}

.pagination__item {
  font-family: bebas, Roboto, Open-Sans, sans-serif;
  color: #fff;
  font-size: 14px;
  background-color: #f3af9d;
  text-align: center;
  vertical-align: middle;
  padding: 5px;
  min-width: 30px;
  min-height: 30px;
  text-decoration: none;
}

.pagination__item:hover {
  background-color: #c53c18;
}

.pagination__item--isactive {
  background-color: #e8603c;
}

.aside {
  max-width: 220px;
  padding-top: 5px;
}

.aside__block {
  margin-bottom: 20px;
}

.aside__title {
  text-transform: uppercase;
  color: #e8603c;
  font-family: bebas, Roboto, Open-Sans, sans-serif;
  font-size: 14px;
  margin-bottom: 15px;
  position: relative;
  overflow: hidden;
}

.aside__title::after {
  content: '';
  position: absolute;
  top: 0;
  margin-left: 20px;
  height: 17px;
  width: 100%;
  background-image: url(../img/post_pt.jpg);
}

.aside__text {
  line-height: 1.2;
}

.aside__link {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.aside__link__item {
  text-decoration: none;
  color: #464646;
}

.aside__link__item:hover {
  text-decoration: underline;
  color: black;
}

.aside__link__item:active {
  color: #e8603c;
}

.aside__photo {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 5px;
}

.aside__photo__item {
  background-color: #f3af9d;
  border: 1px solid #e8603c;
  min-height: 50px;
  min-width: 50px;
  box-sizing: border-box;
  will-change: transform;
  transition: transform .3s ease-in;
}

.aside__photo__item:hover {
  transform: scale(1.3);
  z-index: 3;
}
