.aside {
    max-width: 220px;
    padding-top: 5px;


    &__block {
        margin-bottom: 20px; }

    &__title {
        text-transform: uppercase;
        color: $accent;
        font-family: $bebas;
        font-size: 14px;
        margin-bottom: 15px;
        position: relative;
        overflow: hidden;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            margin-left: 20px;
            height: 17px;
            width: 100%;
            background-image: url(../img/post_pt.jpg); } }

    &__text {
        line-height: 1.2; }

    &__link {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        &__item {
            text-decoration: none;
            color: $primer;

            &:hover {
                text-decoration: underline;
                color: darken($primer, 80); }

            &:active {
                color: $accent; } } }

    &__photo {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-gap: 5px;

        &__item {
            background-color: $pink-light;
            border: 1px solid $accent;
            min-height: 50px;
            min-width: 50px;
            box-sizing: border-box;
            will-change: transform;
            transition: transform .3s ease-in;


            &:hover {
                transform: scale(1.3);
                z-index: 3; } } } }
