.title {
    display: block;
    height: 60px;
    width: 100%;
    background-color: $accent;
    font-family: $bebas;
    text-transform: uppercase;
    color: $white;
    font-size: 24px;
    position: relative;
    padding: 10px 20px;
    margin-bottom: 20px;

    &::before,
    &::after {
        content: '';
        height: 60px;
        background-color: $pink-light;
        width: 100%;
        display: block;
        position: absolute;
        top: 0; }

    &:after {
        margin-left: 10px;
        left: 100%; }

    &::before {
        margin-right: 10px;
        right: 100%; } }

.post {
    &::before {}
    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 15px;
        background-image: url(../img/post_pt.jpg);
        margin-bottom: 50px; }

    &__data {

        &__info {
            min-width: 145px;
            padding-top: 20px;
            align-items: flex-start; }

        &__title {
            text-transform: uppercase;
            color: $accent;
            font-family: $bebas;
            margin-bottom: 10px;
            font-size: 12px; }

        &__text {
            color: $nav;
            font-family: $helvetica;
            font-size: 12px;
            margin-bottom: 10px;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
                color: darken($nav, 75); } }

        &__link {
            text-decoration: none;
            color: $nav;
            margin-left: 5px;

            &::before {
                content: '|';
                color: inherit;
                font-size: inherit;
                margin-right: 5px; }

            &:first-child {
                margin-left: 0;
                padding: 0;

                &::before {
                    content: '';
                    font-size: 0; } }

            &:hover {
                text-decoration: underline;
                color: darken($nav, 50); }

            &:active {
                color: $accent; } } }

    &__content {
        padding: 5px;
        padding-top: 20px;
        margin-bottom: 10px;
        text-align: left;

        &__title {
            text-transform: uppercase;
            color: $accent;
            font-size: 18px;
            font-family: $bebas;
            margin-bottom: 10px; }

        &__text {
            color: $nav;
            font-family: $helvetica;
            font-size: 12px;
            margin-bottom: 50px;
            color: $primer;

            & > p {
                margin-bottom: 15px;
                line-height: 1.6; } }

        &__link {
            text-decoration: none;
            color: $nav;
            position: relative;
            top: -10px;

            &:hover {
                text-decoration: underline;
                color: darken($nav, 50); }

            &:active {
                color: $accent; } } }

    &__preview {
        padding-bottom: 20px; }

    &__simple {

        &__text {
            background-color: $quote_bg;
            font-style: italic;
            padding-top: 25px;
            padding-right: 40px;
            padding-bottom: 20px;
            padding-left: 80px;
            display: inline-block;
            margin-bottom: 50px;
            position: relative;
            letter-spacing: .4px;

            &::before {
                content: '';
                width: 35px;
                height: 20px;
                display: block;
                background-image: url(../img/quotes.svg);
                background-repeat: no-repeat;
                display: inline-block;
                position: absolute;
                left: 35px;
                font-size: 13px; } } } }


.pagination {
    display: grid;
    grid-template-columns: repeat(3, minmax(30px, max-content));
    grid-gap: 5px;

    &__item {
        font-family: $bebas;
        color: $white;
        font-size: 14px;
        background-color: $pink-light;
        text-align: center;
        vertical-align: middle;
        padding: 5px;
        min-width: 30px;
        min-height: 30px;
        text-decoration: none;

        &:hover {
            background-color: darken($pink-light, 35); }

        &--isactive {
            background-color: $accent; } } }


